import styles from "./page.module.css";

export default function Home() {
  return (
    <main className={styles.main}>
      <h1 className={styles.titulo}>Nuevo Producto</h1>
      <span>Imagen</span>
      <div className={styles.contendeorM}>
        <img src="" alt="" />
      </div>
      <section>
        <span>Nombre</span>
        <input type="text" placeholder="Nombre" />
        <span>Categoria</span>
        <input type="text" />
        <span>Marca</span>
        <input type="text" />
        <span>Precio</span>
        <input type="text" />
        <span>Cantidad</span>
        <input type="text" />
        <span>Color</span>
        <input type="text" />
      </section>
      <div className={styles.orden}>
        <button className={styles.bto}>Descargar QR</button>
      </div>
    </main>
  );
}
